@-webkit-keyframes toRightFromLeft {
    30% {
        -webkit-transform: translate(30%);
        transform: translate(30%)
    }

    31% {
        -webkit-transform: translate(-50%);
        opacity: 0;
        transform: translate(-50%)
    }

    to {
        opacity: 1
    }
}

@keyframes toRightFromLeft {
    30% {
        -webkit-transform: translate(30%);
        transform: translate(30%)
    }

    31% {
        -webkit-transform: translate(-50%);
        opacity: 0;
        transform: translate(-50%)
    }

    to {
        opacity: 1
    }
}

@-webkit-keyframes toLeftFromRight {
