@-webkit-keyframes toBottomFromTop {
    30% {
        -webkit-transform: translateY(-100%);
        transform: translateY(-100%)
    }

    31% {
        -webkit-transform: translateY(80%);
        opacity: 0;
        transform: translateY(80%)
    }

    to {
