@-webkit-keyframes fadeInTop {
    0% {
        -webkit-transform: translateY(20%);
        opacity: 0;
        transform: translateY(20%)
    }

    to {
        opacity: 1
    }
}

@keyframes fadeInTop {
    0% {
        -webkit-transform: translateY(20%);
        opacity: 0;
        transform: translateY(20%)
    }

    to {
        opacity: 1
    }
}

@-webkit-keyframes fadeOutBottom {
    0% {
        opacity: 1
    }

    to {
